import api from "./api"
export default {
	addNum({ commit, state }, dapa) {
		commit('REMBER_ANSWER', { dapa })
	},
	addCketList({commit,state},data){
		api.ajaxGet('/ticket/list',data)
       .then(res => {
              console.log(res);
			  commit('CKET_LIST',res)
          })
        .catch(res =>{
          console.log(res);
        })
	},
	addDrawalList({commit,state},data){
		api.ajaxGet('/account/txrecord',data)
       .then(res => {
              console.log(res);
			  commit('CKET_LIST',res)
          })
        .catch(res =>{
          console.log(res);
        })
	}
}


